import React, { Component } from 'react';
import styled from 'styled-components';


const HeaderWrapper = styled.div`
  background-color: #222;
  height: 100px;
  padding: 20px;
  color: white;
`


class Header extends Component {
  render() {
    return (
      <HeaderWrapper>
        <h2>Hilmar Ragnarsson</h2>
      </HeaderWrapper>
    );
  }
}

export default Header;



